.panel {
  height: 100%;
  justify-content: center;
  align-items: center;
}
.scoreboard {
  position: absolute;
  text-align: center;
  height: 5%;
  top: 120px;
  font-size: 2em;
  line-height: 2;
  background-color: #fff;
  border: 1px solid #bdbdbd;
  border-radius: 30px;
  padding: 0 40px;
  z-index: 1001;
}
.card-wrapper {
  width: 50%;
  text-align: center;
  display: flex;
  justify-content: center;
}
.card-wrapper .description {
  font-size: 2em;
  font-weight: 1000;
}
.versus {
  position: absolute;
  font-size: 5em;
  background-color: #fff;
  z-index: 1000;
}
.versus-line {
  position: absolute;
  height: 100%;
  border: 1px solid #5f5f5f;
}
